img {
  max-width: 100%;
  height: auto;
}
.product {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  margin: 10px;
  padding: 20px;
  width: 100%;
  /* height: 800px; */
  max-height: 300px;
  min-width: 100px;
  background-color: white;
  z-index: 1;
  box-shadow: 0 6px 18px -9px rgba(0, 0, 0, 0.75);
  transition: transform 0.1s ease-in;
  
}
.product > img {
  max-height: 150px;
  width: 100%;
  object-fit:contain;
 
  /* margin-bottom: 25px; */
  /* padding-bottom: 25px; */
}
.product__price {
  margin-top: 5px;
}

.product__info {
  height: 70px;
  margin-bottom: 15px;
  padding-bottom: 50px;
}
.product > button {
  background: #f0c14b;
  border: 1px solid;
  margin-top: 10px;
  border-color: #a88734 #9c7e31 #846a29;
  color: #111;
}
.product__rating {
  display: flex;
}
.product:hover {
  transform: scale(1.05);
  background-color:#faf6f6 ;
  
  
}
.product > img :hover{
  background-color:#f5efef;
}
.btn{
  width: 50%;
  cursor: pointer;
  border-radius: 4px;

}
.btn:hover {
  background-color: #f4d27e;
}

.btn:active {
  background-color: #f3c808;
  box-shadow: 0 5px #b18802;
  transform: translateY(4px);
}
.rating_star img{
  width: 18px;
}