.header {
  height: 60px;
  display: flex;
  align-items: center;
  background-color: #131921;
  position: sticky;
  top: 0;
  z-index: 100;
}
.header__logo {
  width: 100px;
  object-fit: contain;
  margin: 0 20px;
  margin-top: 18px;
}
.header__searchIcon {
  padding: 5px;
  height: 22px !important;
  background-color:#FEBD69 ;
}
.header__searchIcon:hover {
  background-color:#cd9042 ;
}
.header__search {
  display: flex;
  flex: 1;
  align-items: center;
  border-radius: 24px;
  cursor: pointer;
}
.header__searchInput {
  height: 11px;
  padding: 10px;
  border: none;
  width: 80%;
  margin-left: 50px;
  align-items: right;
  
  
}
.header__option {
  display: flex;
  flex-direction: column;
  margin-left: 10px;
  margin-right: 15px;
  color: white;
}
.header__nav {
  display: flex;
  justify-content: space-evenly;
  margin-right: 10px;
}
.header__optionLineOne {
  font-size: 10px;
}

.header__optionLineTwo {
  font-size: 13px;
  font-weight: 800;
}

.header__optionBasket {
  display: flex;
  align-items: center;
  color: white;
  margin-top: 5px;
}
.header__basketCount {
  position: absolute;
  left: 100;
  top:8px;
  font-size: 12px;
  margin-left: 10px;
  margin-right: 10px;
}
.header__clearlink {
  text-decoration: none;
}
.address{
  color: #fff;
}
.hello {
  width: 170px;
  position: absolute;
  left:165px;
  top: 8px;
  color: #868585;

}
