img {
  max-width: 100%;
  height: auto;
}

.home {
  /* display: flex;
  justify-content: center; */
  margin-left: auto;
  margin-right: auto;
  max-width: 1500px;
 
}
.home__image {
  width: 100%;
  z-index: 1;
  margin-bottom: -400px;
  /* mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0)); */
}
.home__row {
  display: flex;
  z-index: 1;
  margin-left: 5px;
  margin-right: 5px;
  margin-top: 55px;
  height: 420px;
  width: 100%;
  
}
@media only screen and (max-width: 768px) {
  /* For mobile phones: */
  [class*="col-"] {
    width: 100%;
  }
  .home__row{
    display: block;
    height: auto;
    width:auto;
  }
  .home__image{
    margin-bottom:-60px;
  }
}

